@charset "utf-8";

/* template ---------------------------------------------------- */

html , body
{
	margin: 0;
	padding: 0;
}
body
{
	margin:0;
	padding:0;
	overflow-x:hidden;
	overflow-y:auto;
	background:#f9f9f9;
	font-size:16px;
	font-family: "Century Gothic" , Arial, "文泉驛正黑", "WenQuanYi Zen Hei", "儷黑 Pro", "LiHei Pro", "微軟正黑體", "Microsoft JhengHei", "新細明體", DFKai-SB, sans-serif;
	line-height:1.8;
	color:#000;
}

input , dd , button , select
{
	font-family: "Century Gothic" , Arial, "文泉驛正黑", "WenQuanYi Zen Hei", "儷黑 Pro", "LiHei Pro", "微軟正黑體", "Microsoft JhengHei", "新細明體", DFKai-SB, sans-serif;
}

.text-center {text-align:center !important;}
.text-right {text-align:right !important;}

.float-left {float:left;}
.float-right {float:right;}

a
{
	color:#369;
	text-decoration:underline;
}
a:hover
{
	color:#036;
	text-decoration:none;
}

.hidden
{
	display:none;
}

.wrap
{
	width:980px;
	margin:0 auto;
	position:relative;
}

.forPC
{
	display:block;
}

.forMobile
{
	display:none;
}

/* template ---------------------------------------------------- */

.header
{
	height:65px;
	background:#FFF;
}

.header .logo
{
	position:absolute;
}

.header .logo a
{
	display:block;
	width:180px;
	height:65px;
	background:url(/assets/images/logo1.png) no-repeat 0 8px;
	background-size:150px auto;
	cursor:pointer;
}

.header ul.menu
{
	float:right;
}

.header ul.menu>li
{
	display:inline-block;
	height:65px;
	list-style-type:none;
	line-height:65px;
	margin-left:15px;
	position:relative;
}

.header ul.menu>li>a
{
	color:#000;
	text-decoration:none;
	font-size:14px;
}

.header ul.menu>li>a:hover ,
.header ul.menu>li:hover>a
{
	color:#717F74;
}

.header ul.menu ul
{
	display:none;
	position:absolute;
	z-index:999;
	left:-5px;
	top:55px;
	background:#717F74;
}

.header ul.menu ul li
{
	/*min-width:300px;*/
	height:35px;
	line-height:35px;
	list-style-type:none;
	border-top:1px solid #80967E;
}

.header ul.menu li:nth-child(2) ul
{
	width:350px;
}

.header ul.menu li:nth-child(3) ul
{
	width:400px;
}

.header ul.menu li:nth-child(4) ul
{
	width:130px;
}

.header ul.menu li:nth-child(5) ul
{
	width:170px;
}

.header ul.menu li:nth-child(6) ul
{
	width:170px;
}

.header ul.menu ul li a
{
	display:block;
	width:100%;
	color:#FFF;
	font-size:15px;
	line-height:35px;
	padding-left:10px;
	text-decoration:none;
}

.header ul.menu ul li a:hover ,
.header ul.menu ul li:hover
{
	color:#FF0;
	background:#768C87;
}

.content
{
	background:#f9f9f9;
}

.footer .link
{
	background:#f9f9f9;
}

.footer .link .wrap
{
	width:800px;
	margin:0 auto;
	background:#FFF;
}

.footer .link .link_title
{
	width:760px;
	height:30px;
	margin:0 auto;
	position:relative;
	padding-top:30px;
}

.footer .link .link_title hr
{
	margin:0;
	border:0;
	border-top:1px solid #999;
}

.footer .link .link_title div
{
	position:absolute;
	display:block;
	width:100%;
	top:13px;
	text-align:center;
}

.footer .link .link_title span
{
	font-size:18px;
	background:#FFF;
	padding:5px;
}

.footer .link .banner img
{
	height:55px;
	margin:10px 20px;
}

.footer .copyright
{
	height:120px;
	background:#666;
	padding-top:30px;
}

.footer .copyright p
{
	text-align:center;
	font-size:15px;
	color:#FFF;
	line-height:25px;
}

/* index ---------------------------------------------------- */

.content.index .wrap
{
	background:#FFF;
	width:800px;
	padding-top:80px;
}

.mainpic .img
{
	height:400px;
	width:980px;
	background:url(/assets/images/mainpic.jpg) center -10px;
}

.mainpic .box
{
	position:absolute;
	border:2px solid #FFF;
	top:300px;
}

.mainpic.page .box
{
	top:160px;
}

.mainpic .box a
{
	display:block;
	width:178px;
	height:178px;
	color:#FFF;
	text-align:center;
	line-height:210px;
}

.mainpic .box a:hover
{
	text-decoration:none;
}

.mainpic .box1
{
	z-index:1001;
	left:220px;
	background:#D9BD7B url(/assets/images/index_icon1.png) no-repeat center 58px;
}

.mainpic .box2
{
	z-index:1002;
	left:400px;
	background:#B6633D url(/assets/images/index_icon2.png) no-repeat center 55px;
}

.mainpic .box3
{
	z-index:1003;
	left:580px;
	background:#717F74 url(/assets/images/index_icon3.png) no-repeat center 55px;
}

.content.index .title
{
	font-size:26px;
	color:#000;
	line-height:80px;
	text-align:center;
}

.content.index .box
{
	width:800px;
	height:230px;
	position:relative;
}

.content.index .box ul
{
	position:absolute;
	left:430px;
	top:30px;
}

.content.index .box ul li
{
	display:block;
	list-style-type:none;
	width:350px;
}

.content.index .box ul li.stitle
{
	font-size:20px;
	color:#000;
	line-height:25px;
	text-align:center;
}

.content.index .box ul li.txt
{
	font-size:12px;
	color:#666;
	line-height:20px;
	height:80px;
	overflow:hidden;
	margin-top:15px;
	letter-spacing:1px;
	/*
	white-space: nowrap;
	text-overflow: ellipsis;
	text-align: justify;
	*/
}

.content.index .box ul li.more
{
	text-align:right;
	margin-top:15px;
}

.content.index .box ul li.more a
{
	font-size:16px;
	line-height:20px;
	color:#999;
	text-decoration:none;
}

.content.index .box2
{
	text-align:right;
}


.content.index .box2 ul
{
	left:30px;
}

.content.index .box2 ul li
{
	text-align:left;
}

.content.index .vedio
{
	margin-top:10px;
}

/* page ---------------------------------------------------- */


.content.page .wrap
{
	min-height:500px;
	background:url(/assets/images/pagepic.jpg) no-repeat center -20px;
	padding-top:110px;
}

.content.page .article
{
	margin:0 auto;
	width:750px;
	padding:25px;
	background:#FFF;
}

.content.page .article .links
{
	padding-bottom:20px;
	border-bottom:1px dashed #CCC;
}

.content.page .article .links li
{
	margin:0;
	padding-left:30px;
	list-style-type:none;
	line-height:30px;
	background:url(/assets/images/icon_arrow.png) no-repeat;
}

.content.page .article .links li a.active
{
	color:#CCC;
	text-decoration:none;
}

.content.page .bread-crumbs
{
	padding:25px 0;
	color:#999;
	font-size:15px;
}

.content.page .bread-crumbs a
{
	color:#999;
	text-decoration:none;
}

.content.page .bread-crumbs a:hover
{
	color:#666;
	text-decoration:underline;
}

.content.page .article h1
{
	font-size:24px;
	color:#000;
	line-height:50px;
	text-align:center;
	font-weight:normal;
	/*margin:20px 0;*/
}

.content.page .article h2
{
	font-size:20px;
	color:#333;
	line-height:35px;
	margin:10px 0;
}

.content.page .article h3
{
	font-size:16px;
	color:#333;
	line-height:35px;
	margin:5px 0;
}

.content.page .article p
{
	font-size:16px;
	color:#000;
	line-height:25px;
	margin:10px 0;
	text-align:justify;
}

.content.page .article p small
{
	font-size:75%;
}

.content.page .article ol,
.content.page .article ul
{
	margin-left:25px;
}

.content.page .article ol li,
.content.page .article ul li
{
	margin:10px 0;
}

.content.page hr
{
	border:0;
	border-top:1px dashed #999;
}

.content.page .gototop
{
	float:right;
	width:25px;
	height:25px;
	border-radius:50%;
	background:#717F74 url(/assets/images/icon_arrow_up.png) no-repeat center center;
	cursor:pointer;
}

.content.page .photos
{
	text-align:start !important;
	display:block;
	margin-left:40px;
}

.content.page .photos a
{
	display:block;
	float:left;
	width:150px;
	height:150px;
	line-height:140px;
	border:1px solid #CCC;
	padding:5px;
	margin:6px 3px;
	text-align:center;
	vertical-align:middle;
}

.content.page .photos a img
{
	vertical-align:middle;
}

.content.page table
{
	border-collapse:collapse;
}

.content.page table th
{
	background-color:#666;
	color:#FFF;
}

.content.page table th,
.content.page table td
{
	border:1px solid #999;
	padding:5px;
}


/* lightbox ---------------------------------------------------- */

div.box_popup
{
	display:none;
	position:absolute;
	text-align:center;
	width:750px;
	height:530px;
	line-height:530px;
	padding:35px 30px;
	color:#333;
	background:#FFF url(/assets/images/loading.gif) no-repeat center center;
	border-radius:10px;
}

div.box_popup p,
div.box_popup img
{
	vertical-align:middle;
}

div.box_popup #closeBtn
{
	width:35px;
	height:35px;
	position:absolute;
	right:5px;
	top:5px;
	cursor:pointer;
	text-align:center;
	line-height:35px;
	background:url(/assets/images/icon_close.png);
}

div.box_popup #arrow_right ,
div.box_popup #arrow_left
{
	position:absolute;
	top:270px;
	width:35px;
	height:42px;
	cursor:pointer;
}

div.box_popup #arrow_right
{
	right:5px;
	background:url(/assets/images/arrow_right.png);
}

div.box_popup #arrow_left
{
	left:5px;
	background:url(/assets/images/arrow_left.png);
}

#lightbox
{
	/*底下lightbox灰底基本的五行CSS設定請務必寫好*/
	z-index: 9999;
	display: none;
	background: #000;	/* color */
	/*position:absolute;*/
	position:fixed;
	top:0;
	left:0;
}

form
{
	padding:20px;
}

form label
{
	display:block;
	line-height:35px;
	font-size:18px;
	color:#333;
	vertical-align:middle;
	margin-bottom:10px;
}

form span
{
	display:inline-block;
	width:300px;
	text-align:right;
}

form input
{
	width:250px;
	height:25px;
	line-height:20px;
	margin-left:10px;
}

form button
{
	padding:10px;
	border-radius:5px;
	border:1px solid #CCC;
	background-color:#FFF;
}




